

.homeSection {
    background-color: #282c34;
    height: 100%;
    left: 0;
    position: absolute;
    top: 0;
    width: 100vw;
    /*overflow: hidden;*/

}

.particles {
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100%;
}
.intro {
    position: absolute;
    top: calc((100% - 114px) / 2);;
    left: calc((100% - 620px) / 2);
    z-index: 1;
    font-size: 32pt;
    line-height: 36pt;
    text-align: center;
    width: fit-content;
    margin: 0 auto;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;

}
.line {
    width: 620px;
    background-color: #BBD336;
    height: 3px;
    padding: 0;
}

@media screen and (max-width: 759px) {
    .intro-text {
        font-size: 18pt;
    }
    .line {
        width: 360px;
    }
}


@media screen and (max-width: 400px) {
    .intro-text {
        font-size: 16pt;
    }
    .line {
        width: 260px;
    }
}

@media screen and (max-width: 300px) {
    .intro-text {
        font-size: 16pt;
        overflow: hidden;
    }
    .line {
        width: 100%;
    }
}

.highlight {
    color: #e31b6d;
    font-family: "Raleway-semibold";
}