.navBar {
  display: flex;
  position: fixed;
  top: 10px;
  right: 20px;
  color: white;
  z-index: 1;
  flex-direction: row;

  a {
    text-decoration: none;
    font-size: 18pt;
    margin-right: 10px;
    padding: 0 6px 0 6px;

    button {
      z-index: 2;
      background-color: white;
    }
  }
}


a:checked {
  color: red;
  background-color: #6f42c1;
}